<template>
  <section class="hero">
    <div class="hero-body">
      <p class="title">Peikai Tan</p>
      <p class="subtitle">Force Network Diagram (Covid)</p>
    </div>
  </section>
  <div class="container">
    <div class="columns">
      <div class="column">
        <!-- <button class="button is-fullwidth" id="group1">Multiple Groups</button>
        <button class="button is-fullwidth" id="group2">One Center</button> -->
      </div>
      <div class="column is-three-fifths">
        <svg></svg>
      </div>
      <div class="column">
        <div id="properties" v-if="properties.Age">
          Age: {{ properties.Age }}<br />
          Vaccinated: {{ properties.Vaccinated }}<br />
          Occupation: {{ properties.Occupation }}<br />
          Nationality: {{ properties.Nationality }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as d3 from "d3";

export default {
  name: "App",
  data() {
    return {
      width: 800,
      height: 800,

      circleRadius: 10,
      radius: 50,

      covidData: null,

      properties: {
        Age: null,
        Nationality: null,
        Occupation: null,
        Vaccinated: null,
      },
    };
  },
  methods: {
    resetProperties() {
      this.properties = {
        Age: null,
        Nationality: null,
        Occupation: null,
        Vaccinated: null,
      };
    },
  },
  components: {},
  mounted() {
    Promise.all([
      d3.json(
        "https://raw.githubusercontent.com/Chi-Loong/CSC3007/main/assignments/links-sample.json"
      ),
      d3.json(
        "https://raw.githubusercontent.com/Chi-Loong/CSC3007/main/assignments/cases-sample.json"
      ),
    ]).then((data) => {
      data[0].forEach((e) => {
        e.source = e.infector;
        e.target = e.infectee;
      });

      this.covidData = data;

      let svg = d3
        .select("svg")
        .attr("viewBox", "0 0 " + this.width + " " + this.height);

      let colorScale = d3
        .scaleOrdinal()
        .domain(d3.range(this.groups))
        .range(d3.schemeDark2);

      var link = svg
        .selectAll("line")
        .data(this.covidData[0])
        .enter()
        .append("line")
        .style("stroke", "#aaa");

      let node = svg
        .append("g")
        .attr("id", "nodes")
        .selectAll("circle")
        .data(this.covidData[1])
        .enter()
        .append("circle")
        .attr("r", this.circleRadius)
        .style("fill", (d) => {
          return colorScale(d.gender === "female" ? 0 : 1);
        })
        .on("mouseover", (d, i) => {
          d3.select(d.target)
            .transition()
            .duration("50")
            .attr("opacity", ".85");

          this.properties = {
            Age: i.age,
            Nationality: i.nationality,
            Occupation: i.occupation,
            Vaccinated: i.vaccinated,
          };
        })
        .on("mouseout", (d) => {
          d3.select(d.target).transition().duration("50").attr("opacity", "1");

          this.resetProperties();
        });

      d3.forceSimulation(this.covidData[1])
        .force(
          "link",
          d3
            .forceLink()
            .id((d) => {
              return d.id;
            })
            .links(this.covidData[0])
        )
        .force(
          "x",
          d3
            .forceX()
            .strength(0.1)
            .x(this.width / 2)
        )
        .force(
          "y",
          d3
            .forceY()
            .strength(0.1)
            .y(this.height / 2)
        )
        .force("charge", d3.forceManyBody().strength(-30))
        .force("collide", d3.forceCollide().strength(0.1).radius(this.radius))
        .on("tick", ticked);

      function ticked() {
        link
          .attr("x1", (d) => {
            return d.source.x;
          })
          .attr("y1", function (d) {
            return d.source.y;
          })
          .attr("x2", function (d) {
            return d.target.x;
          })
          .attr("y2", function (d) {
            return d.target.y;
          });

        node
          .attr("cx", function (d) {
            return d.x;
          })
          .attr("cy", function (d) {
            return d.y;
          });
      }
    });

    // d3.select("#group1").on("click", function () {
    //   simulation
    //     .force(
    //       "x",
    //       d3
    //         .forceX()
    //         .strength(0.5)
    //         .x((d) => xPosition(d.class))
    //     )
    //     .force(
    //       "y",
    //       d3
    //         .forceY()
    //         .strength(0.5)
    //         .y(height / 2)
    //     )
    //     .alphaTarget(0.3);
    //   // .restart();
    // });

    // d3.select("#group2").on("click", function () {
    //   simulation
    //     .force("x", d3.forceX().strength(0.1).x(400))
    //     .force("y", d3.forceY().strength(0.1).y(400))
    //     .alphaTarget(0.3);
    //   // .restart();
    // });
  },
};
</script>

